import React from 'react'
import cx from 'classnames'

const SwipeEntitiesListTab = ({ onClick, text, badge, isActive, name, className }) => {
  const containerClass = cx({
    'swipe-entities-list-tab__link': true,
    'swipe-entities-list-tab__link--active': isActive,
    [className]: className
  })

  const handleClick = (event) => onClick(event, name)

  return (
    <div
      onClick={handleClick}
      className={containerClass}
    >
      <div className='swipe-entities-list-tab__text'>
        {text}
      </div>
      {badge !== undefined && <div className='swipe-entities-list-tab__badge'>{badge}</div>}
    </div>
  )
}

export default SwipeEntitiesListTab



