import React, { Fragment, PureComponent } from 'react'
import PropTypes from 'prop-types'
import withTranslations from 'Shared/hoc/withTranslations'
import Divider from 'Shared/components/FormElements/Divider'
import MembershipEmptyState from './components/MembershipEmptyState'
import MembershipServices from './components/MembershipServices'
import MembershipNotificationAlertConnector from 'Employer/connectors/MembershipNotificationAlert/MembershipNotificationAlertConnector'
import Loader from 'Shared/components/Loader/Loader'
import './MembershipDetails.scss'
import { Helmet } from 'react-helmet'
import LocationContactConnector from 'Shared/connectors/LocationContact/LocationContactConnector'

class MembershipDetails extends PureComponent {
  componentDidMount () {
    this.props.fetchCurrentMembershipPlan()
    this.props.isAdminEmployer && this.props.fetchActiveRecurringTransactions()
  }

  render () {
    const {
      t,
      isLoading,
      hasCurrentMembership,
      enabledServices,
      enabledAdditionalServices,
      isCurrentMembershipMonthlyContinuous,
      hasActiveCreditsRecurringTransactions,
      activeCreditsRecurringTransaction,
      cancelRecurringTransaction,
      isAdminEmployer
    } = this.props

    return (
      <Fragment>
        <Helmet>
          <title>{t('meta.employer.title.settings')}</title>
          <meta property='og:title' content={t('meta.employer.title.settings')} />
        </Helmet>
        {
          isLoading ? <Loader description={`${t('common.loading')}...`} /> : (
            <div>
              <MembershipNotificationAlertConnector />
              {
                hasCurrentMembership ? (
                  <div className='membership-details__container'>
                    <div>
                      <h3>{t('employer.settings.membershipDetails')}</h3>
                      <Divider className='membership-details__divider' />
                      <MembershipServices
                        enabledServices={enabledServices}
                        enabledAdditionalServices={enabledAdditionalServices}
                        isCurrentMembershipMonthlyContinuous={isCurrentMembershipMonthlyContinuous}
                        hasActiveCreditsRecurringTransactions={hasActiveCreditsRecurringTransactions}
                        activeCreditsRecurringTransaction={activeCreditsRecurringTransaction}
                        cancelRecurringTransaction={cancelRecurringTransaction}
                        isAdminEmployer={isAdminEmployer}
                      />
                      <Divider className='membership-details__divider' />
                      <div className='membership-details__section'>
                        <div>
                          {t('employer.settings.questions')}&nbsp;
                          <LocationContactConnector />
                        </div>
                      </div>
                    </div>
                  </div>
                ) : <MembershipEmptyState />
              }
            </div>
          )
        }
      </Fragment>
    )
  }
}

MembershipDetails.propTypes = {
  fetchCurrentMembershipPlan: PropTypes.func.isRequired,
  enabledServices: PropTypes.array.isRequired,
  enabledAdditionalServices: PropTypes.array.isRequired,
  hasCurrentMembership: PropTypes.bool.isRequired,
  isCurrentMembershipMonthlyContinuous: PropTypes.bool.isRequired
}

export default withTranslations(MembershipDetails)



