import * as R from 'ramda'

export const hasAnySocialMedia = socialMediaLinksObject => R.pipe(
  Object.values,
  R.any(R.complement(R.isEmpty))
)(socialMediaLinksObject)

/**
 * Returns an object that is mapped in the component
 * @param socialMediaLinksObject
 * @return {{type: string, url: string}[]}
 */
export const mapSocialMedia = socialMediaLinksObject => {
  return Object.keys(socialMediaLinksObject)
    .map(medium => {
      return {
        type: medium,
        url: socialMediaLinksObject[medium]
      }
    })
    .filter(medium => !!medium.url)
}

export const getPermissionsPayload = R.pipe(
  R.omit(['admin', 'emails', 'jobOffers']),
  R.pickBy(R.equals(true)),
  R.keys
)

export const getJobOfferIdsPayload = R.pipe(
  R.propOr([], 'jobOffers'),
  R.map(R.prop('value'))
)

export const getEmailsPayload = R.pipe(
  R.propOr([], 'emails'),
  R.map(R.pipe(
    R.prop('value'),
    R.objOf('email')
  ))
)

export const generateAddTeamMemberPayload = values => ({
  emails: getEmailsPayload(values),
  permissions: getPermissionsPayload(values),
  jobOfferIds: getJobOfferIdsPayload(values),
  organisationRole: values.organisationRole
})

export const generateEditTeamMemberPayload = values => ({
  permissions: getPermissionsPayload(values),
  jobOfferIds: getJobOfferIdsPayload(values),
  organisationRole: values.organisationRole
})



