import React from 'react'
import PersonalDetailsFormConnector from 'Employer/connectors/Forms/PersonalDetails/PersonalDetailsFormConnector'
import EmployerExportDataConnector from 'Employer/connectors/ExportData/EmployerExportDataConnector'
import DeleteEmployerAccountSectionConnector from 'Employer/connectors/DeleteAccountSection/DeleteEmployerAccountSectionConnector'
import Divider from 'Shared/components/FormElements/Divider/index'
import withTranslations from 'Shared/hoc/withTranslations'
import '../SettingsView.scss'
import EmployerChangePasswordFormConnector from 'Employer/connectors/Forms/Settings/EmployerChangePasswordFormConnector'
import EmployerRequestChangeEmailConnector from 'Employer/connectors/Forms/Settings/EmployerRequestChangeEmailConnector'

const PersonalDetailsSettingsView = ({ t }) => (
  <div className='settings-view_personal-details-container'>
    <PersonalDetailsFormConnector />
    <EmployerChangePasswordFormConnector />
    <EmployerRequestChangeEmailConnector />
    <div className='settings-view__section'>
      <h3>{t('manageAccount.manageYourData')}</h3>
      <Divider className='settings-view__divider' />
      <EmployerExportDataConnector />
      <DeleteEmployerAccountSectionConnector />
    </div>
  </div>
)

export default withTranslations(PersonalDetailsSettingsView)



