import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import SettingsView from 'Employer/views/SettingsView'
import { isAdminEmployer } from 'Shared/selectors/user'

const mapStateToProps = state => ({
  isAdminEmployer: isAdminEmployer(state)
})

export default withRouter(connect(mapStateToProps)(SettingsView))



