import React, { Component } from 'react'
import { Field } from 'redux-form'
import { SwitchToggle } from 'Shared/components/FormElements/index'
import withTranslations from 'Shared/hoc/withTranslations/index'
import { PrimaryButton } from 'Shared/components/Button/index'
import Divider from 'Shared/components/FormElements/Divider/index'

import './NotificationsForm.scss'

class NotificationsForm extends Component {
  render () {
    const { t, error, submitting, handleSubmit } = this.props
    return (
      <div className='notifications__container'>
        <h3>{t('candidate.settings.notifications')}</h3>
        <Divider className='notifications__divider' />
        <form className='notifications__form' onSubmit={handleSubmit}>
          <Field
            component={SwitchToggle}
            label={t('forms.applicationNotificationEnabled.placeholder')}
            name='applicationNotificationEnabled'
            format={v => !!v}
          />
          <Field
            component={SwitchToggle}
            label={t('forms.weeklyCandidateNotification.placeholder')}
            name='weeklyCandidateEmailEnabled'
            format={v => !!v}
          />
          <Field
            component={SwitchToggle}
            label={t('forms.weeklyActiveJobSummaryEnabled.placeholder')}
            name='weeklyActiveJobSummaryEnabled'
            format={v => !!v}
          />
          <PrimaryButton
            className='notifications__submit'
            disabled={error || submitting}
            type='submit'>
            {submitting ? `${t('common.loading')}...` : t('common.saveChanges')}
          </PrimaryButton>
        </form>
      </div>
    )
  }
}

export default withTranslations(NotificationsForm)



