import React from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import Label from 'Shared/components/FormElements/Label'
import PendingInviteItemConnector from 'Employer/connectors/TeamMembers/PendingInviteItemConnector'
import './TeamMembers.scss'

const TeamMembersInvites = ({ t, organisationInvitesList }) => (
  <div className='team-members__list-container'>
    <div className='team-members__list-heading-container'>
      <h3 className='team-members__list-heading'>{t('employer.settings.teamMembers.pendingInvitations.title')}</h3>
    </div>
    <div className='team-members__list'>
      <div className='team-members__list-heading'>
        <Label>{t('employer.settings.teamMembers.manageTeamMembers.emailAddress')}</Label>
      </div>
      <div>
        {organisationInvitesList.map(invite => <PendingInviteItemConnector key={invite.id} invite={invite} />)}
      </div>
    </div>
  </div>
)

export default withTranslations(TeamMembersInvites)



