import React from 'react'
import PropTypes from 'prop-types'
import { MultiSelect } from 'Shared/components/FormElements/Select'
import { t } from 'i18next'
import './JobOfferMultiSelect.scss'

const Option = (props) => (
  <div className='job-offer__select--option'>
    <div className='job-offer__select--option-label'>{props.label}</div>
    <div className='job-offer__select--option-description'>
      { props.jobType && <span className='job-offer-card-mobile__details'>{ t(`employer.jobOffers.jobTypes.${props.jobType}`) }{props.commitment && ' • ' + (t(`employer.jobOffers.commitmentTypes.${props.commitment}`))} </span> }
      {
        (
          props.city || props.country) && (
          <span className='job-offer-card-mobile__details'>
            {' • ' + props.city}{(props.city && props.country) && ', '}{props.country}
          </span>
        )
      }
      {' • ' + t(`forms.assignJobPosts.statuses.${props.status}`)}
    </div>
  </div>
)

const Value = (props) => (
  <div className='job-offer__select--value'>
    <div className='job-offer__select--value-label'>{props.label}</div>
  </div>
)

const JobOfferMultiSelect = ({ optionRenderer = Option, valueRenderer = Value, ...rest }) => {
  return <MultiSelect className='job-offer__select' {...rest} optionRenderer={optionRenderer} valueRenderer={valueRenderer} />
}

const defaultPropTypes = {
  label: PropTypes.string.isRequired,
  value: PropTypes.string.isRequired
}

Option.propTypes = defaultPropTypes
Value.propTypes = defaultPropTypes

export default JobOfferMultiSelect



