import isEmail from 'validator/lib/isEmail'

export const isPhoneNumber = value => /^[0-9()\s+-]+$/.test(value)

export const REGEX_TYPE = {
  text: "^\s*[A-Za-z '-]*$",
  number: "^\d+$",
  tel: "^[0-9]{10,14}$",
  email: "[a-z0-9]+@[a-z]+\.[a-z]{2,3}",
}

export const isValidValue = (value, regexRule, required) => {
  if (required && !value) {
    return false
  }

  if (regexRule === 'email') {
    return isEmail(value)
  }

  return value.match(new RegExp(REGEX_TYPE[regexRule], 'g'))
}

export const isEmptyObject = (obj) => {
  const result = Object.keys(obj).length === 0
  return result
}



