import { connect } from 'react-redux'
import PendingInviteItem from 'Employer/components/TeamMembers/PendingInviteItem'
import { deleteOrganisationInvite } from 'Shared/ducks/organisationInvites'

const mapDispatchToProps = (dispatch, ownProps) => ({
  deleteOrganisationInvite: () => dispatch(deleteOrganisationInvite(ownProps.invite.id))
})

export default connect(null, mapDispatchToProps)(PendingInviteItem)



