import React, { Component } from 'react'
import PropTypes from 'prop-types'
import withTranslations from 'Shared/hoc/withTranslations'
import {
  TeamMembersEmptyState,
  TeamMembersInvites,
  TeamMembersList
} from 'Employer/components/TeamMembers'
import '../SettingsView.scss'
import { t } from 'i18next'
import { Helmet } from 'react-helmet'

class TeamMembersSettingsView extends Component {
  componentDidMount () {
    this.props.getOrganisationInvites()
    this.props.getOrganisationEmployers()
    this.props.getTeamMembersAccess()
  }

  render () {
    const { organisationTeamMembersList, organisationInvitesList, organisationAdminEmployers, deleteOrganisationMember } = this.props
    const hasEmptyState = organisationTeamMembersList.length === 0 && organisationInvitesList.length === 0 && organisationAdminEmployers.length <= 1

    return (
      <div>
        <Helmet>
          <title>{t('meta.employer.title.teamMembers')}</title>
          <meta property='og:title' content={t('meta.employer.title.teamMembers')} />
        </Helmet>
        { hasEmptyState ? <TeamMembersEmptyState /> : (
          <div>
            <TeamMembersList
              organisationTeamMembersList={organisationTeamMembersList}
              organisationAdminEmployers={organisationAdminEmployers}
              deleteOrganisationMember={deleteOrganisationMember}
            />
            {organisationInvitesList.length !== 0 && <TeamMembersInvites organisationInvitesList={organisationInvitesList} />}
          </div>
        )}
      </div>
    )
  }
}

TeamMembersSettingsView.propTypes = {
  getOrganisationInvites: PropTypes.func.isRequired,
  getOrganisationEmployers: PropTypes.func.isRequired
}

export default withTranslations(TeamMembersSettingsView)



