import { reduxForm, formValueSelector } from 'redux-form'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import AddTeamMembersForm from 'Employer/forms/Settings/TeamMembers/AddTeamMembersForm'
import { validate, asyncValidate } from './AddTeamMemberFormValidator'
import { inviteTeamMembers } from 'Shared/ducks/organisationInvites'
import { generateAddTeamMemberPayload } from 'Shared/helpers/Organisation'
import { EmployerRoles } from 'Shared/constants/User'

export const formName = 'AddTeamMembersForm'

let Form = reduxForm({
  form: formName,
  validate,
  asyncValidate,
  enableReinitialize: true,
  shouldAsyncValidate: () => true,
  touchOnChange: true,
  asyncChangeFields: ['emails'],
  onSubmit: (values, dispatch) => dispatch(inviteTeamMembers(generateAddTeamMemberPayload(values)))
})(AddTeamMembersForm)

const mapStateToProps = (state) => ({
  selectedRole: formValueSelector(formName)(state, 'organisationRole'),
  initialValues: {
    organisationRole: EmployerRoles.organisation_manager
  }
})

export default withRouter(connect(mapStateToProps)(Form))



