import { reduxForm } from 'redux-form'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import validate from 'Employer/connectors/Forms/PersonalDetails/PersonalDetailsFormValidator'
import PersonalDetails from 'Employer/forms/PersonalDetails/PersonalDetails'
import { updateEmployer } from 'Shared/ducks/user/employer'
import { removeUserAvatar, uploadUserAvatar } from 'Shared/ducks/user'
import { updateUserAttributes } from 'Chat/ducks/classes/users'

export const formName = 'EmployerChangePersonalDetailsForm'

let Form = reduxForm({
  form: formName,
  enableReinitialize: true,
  validate
})(PersonalDetails)

const mapStateToProps = state => ({
  photoUrl: state.user.details.photoUrl,
  initialValues: {
    firstName: state.user.details.firstName,
    lastName: state.user.details.lastName,
    phoneNumber: state.user.details.phoneNumber,
    id: state.user.details.id,
    photoUrl: state.user.details.photoUrl
  }
})

const mapDispatchToProps = (dispatch) => ({
  updateEmployer: async values => {
    await dispatch(updateEmployer(values.id, values))
    dispatch(updateUserAttributes())
  },
  uploadAvatar: async (file, filename) => {
    await dispatch(uploadUserAvatar(file, filename))
    dispatch(updateUserAttributes())
  },
  deleteAvatar: () => dispatch(removeUserAvatar())

})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Form))



