/* eslint-disable no-throw-literal */
import isEmail from 'validator/lib/isEmail'
import OrganisationInvitesService from 'Shared/services/OrganisationInvitesService'
import * as R from 'ramda'

const organisationInvitesService = new OrganisationInvitesService()

const validate = values => {
  const errors = {}

  if (!values['emails']) {
    errors['emails'] = {
      key: 'errors.emailsRequired'
    }
  }

  if (values['emails']) {
    let invalidEmails = []
    values['emails'].forEach(email => !isEmail(email.value) && invalidEmails.push(email.value))

    if (invalidEmails.length > 0) {
      errors['emails'] = {
        key: 'errors.emailsNotValid',
        options: {
          emails: invalidEmails.join(', ')
        }
      }
    }
  }

  return errors
}

const asyncValidate = async (values) => {
  if (values['emails']) {
    let takenEmails = []
    let invitedEmails = []
    let blacklistedEmails = []
    await Promise.all(
      values['emails'].map(async email => {
        try {
          await organisationInvitesService.verifyEmployerEmail(email.value)
        } catch (e) {
          const errorMessage = R.pathOr([], ['response', 'data', 'error', 'message'], e)
          const code = R.propOr('', ['code'], errorMessage[0])

          switch (code) {
            case 400:
              takenEmails.push(email.value)
              throw { emails: {
                key: 'errors.emailsAlreadyTaken',
                options: {
                  emails: takenEmails.join(', ')
                }
              } }
            case 422:
              invitedEmails.push(email.value)
              throw { emails: {
                key: 'errors.emailsAlreadyInvited',
                options: {
                  emails: invitedEmails.join(', ')
                }
              } }
            case 403:
              blacklistedEmails.push(email.value)
              throw { emails: {
                key: 'errors.emailsDomainBlacklist',
                options: {
                  emails: blacklistedEmails.join(', ')
                }
              } }
            default:
              throw { emails: {
                key: 'errors.emailNotProvided'
              } }
          }
        }
      })
    )
  }
}

export {
  validate,
  asyncValidate
}



