import React, { Component } from 'react'
import ReactDOM from 'react-dom'
import Swipe from 'react-easy-swipe'
import './SwipeEntitiesList.scss'
import HideElement from 'Shared/hoc/HideElementOnScroll'
import { isArabicLayout } from 'Shared/helpers/layout'

class SwipeEntitiesListWrapper extends Component {
  state = {
    entitiesWrapperOffset: 0,
    position: 0,
    lastPosition: 0
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    if (this.props.position !== prevProps.position) {
      this.setState({ position: this.getSelectedPosition() })
    }
  }

  onSwipeMove = (position) => this.setState(
    { entitiesWrapperOffset: Math.round(position.x) + this.state.lastPosition },
    () => this.setState({ position: this.getPosition() })
  )

  onSwipeEnd = () => this.setState({ lastPosition: this.getPosition() })

  getPosition = () => {
    const swipeEntitiesListContainer = ReactDOM.findDOMNode(this.swipeEntitiesList)
    const wrapperWidth = window.innerWidth
    const listWidth = swipeEntitiesListContainer.scrollWidth
    const deltaWidth = -(listWidth - wrapperWidth)

    switch (true) {
      case this.state.entitiesWrapperOffset > 0:
        return 0
      case this.state.entitiesWrapperOffset < deltaWidth:
        return deltaWidth
      default:
        return this.state.entitiesWrapperOffset
    }
  }

  getSelectedPosition = () => {
    const swipeEntitiesListContainer = ReactDOM.findDOMNode(this.swipeEntitiesList)
    const listWidth = swipeEntitiesListContainer ? swipeEntitiesListContainer.scrollWidth : 0
    const deltaWidth = -(listWidth - window.innerWidth)
    const positionDeltaWidth = this.state.position - this.props.position

    return Math.abs(positionDeltaWidth) >= Math.abs(deltaWidth) ? deltaWidth : positionDeltaWidth
  }

  getSwipeComponent = () => {
    const navbarPosition = isArabicLayout ? 'right' : 'left'

    return (
      <Swipe onSwipeMove={this.onSwipeMove} onSwipeEnd={this.onSwipeEnd}>
        <div ref={ref => { this.swipeEntitiesList = ref }} className='swipe-entities-list' style={{ [navbarPosition]: `${this.state.position}px` }}>
          {this.props.children}
        </div>
      </Swipe>
    )
  }

  render () {
    const { hideOnScroll } = this.props

    return hideOnScroll ? (
      <HideElement className='swipe-entities-list-wrapper' elementHeight={50} showClass='swipe-entities-list-wrapper--scrolled' hideClass='swipe-entities-list-wrapper--scrolled'>
        {this.getSwipeComponent()}
      </HideElement>
    ) : (
      <div className='swipe-entities-list-wrapper'>
        <Swipe onSwipeMove={this.onSwipeMove} onSwipeEnd={this.onSwipeEnd}>
          {this.getSwipeComponent()}
        </Swipe>
      </div>
    )
  }
}

SwipeEntitiesListWrapper.defaultProps = {
  position: 0,
  hideOnScroll: true
}

export default SwipeEntitiesListWrapper



