import React from 'react'
import Notification from 'Shared/components/Notification/index'
import { Trans } from 'react-i18next'
import './MembershipNotificationAlert.scss'
import { SUCCESS_EMAIL } from 'Shared/constants/Global'
import withTranslations from 'Shared/hoc/withTranslations'

const MembershipNotificationAlert = (props) => {
  const getNotificationText = translateKey => (
    <Trans i18nKey={`employer.settings.membership.alerts.${translateKey}`} className='membership-notification__message'>
      Your membership has expired. Continue using Oliv features by contacting our team at <a href={`mailto:${SUCCESS_EMAIL}`}>{SUCCESS_EMAIL}</a>.
    </Trans>
  )

  switch (true) {
    case props.isCurrentMembershipExpired:
      return <div className='membership-notification__wrapper'>
        <Notification iconClassName='icon-im-info-outline' messageText={getNotificationText('expired')} error />
      </div>
    case props.getCurrentMembershipStatus === 'cancelled':
      return <div className='membership-notification__wrapper'>
        <Notification iconClassName='icon-im-info-outline' messageText={getNotificationText('cancelled')} warning />
      </div>
    case props.getCurrentMembershipStatus === 'inactive':
      return <div className='membership-notification__wrapper'>
        <Notification iconClassName='icon-im-info-outline' messageText={props.t('employer.settings.membership.alerts.inactive')} warning />
      </div>
    default:
      return null
  }
}

export default withTranslations(MembershipNotificationAlert)



