import { connect } from 'react-redux'
import MembershipDetails from 'Employer/components/MembershipDetails/MembershipDetails'
import {
  startMembershipLoading,
  fetchCurrentMembershipIfNeeded,
  finishMembershipLoading
} from 'Shared/ducks/membership'
import {
  fetchActiveRecurringTransactions,
  cancelRecurringTransaction
} from 'Shared/ducks/transactions'
import {
  getCurrentMembershipEnabledServicesList,
  getCurrentMembershipEnabledAdditionalServicesList,
  hasCurrentMembership,
  isCurrentMembershipMonthlyContinuous
} from 'Shared/selectors/membership'
import {
  hasActiveCreditsRecurringTransactions,
  activeCreditsRecurringTransaction
} from 'Shared/selectors/transactions'
import { isAdminEmployer } from 'Shared/selectors/user'

const mapStateToProps = state => ({
  isLoading: state.membership.isLoading,
  enabledServices: getCurrentMembershipEnabledServicesList(state),
  enabledAdditionalServices: getCurrentMembershipEnabledAdditionalServicesList(state),
  hasCurrentMembership: hasCurrentMembership(state),
  isCurrentMembershipMonthlyContinuous: isCurrentMembershipMonthlyContinuous(state),
  hasActiveCreditsRecurringTransactions: hasActiveCreditsRecurringTransactions(state),
  activeCreditsRecurringTransaction: activeCreditsRecurringTransaction(state),
  isAdminEmployer: isAdminEmployer(state)
})

const mapDispatchToProps = dispatch => ({
  fetchCurrentMembershipPlan: async () => {
    try {
      dispatch(startMembershipLoading())
      await dispatch(fetchCurrentMembershipIfNeeded())
      return dispatch(finishMembershipLoading())
    } catch (e) {
      return dispatch(finishMembershipLoading())
    }
  },
  fetchActiveRecurringTransactions: () => dispatch(fetchActiveRecurringTransactions()),
  cancelRecurringTransaction: id => dispatch(cancelRecurringTransaction(id))
})

export default connect(mapStateToProps, mapDispatchToProps)(MembershipDetails)



