import React, { Component, Fragment } from 'react'
import { SmallJustIconButton } from 'Shared/components/Button'
import withTranslations from 'Shared/hoc/withTranslations'
import EditTeamMemberFormConnector from 'Employer/connectors/Forms/Settings/EditTeamMemberFormConnector'
import Modal from 'Shared/components/Modal'

class EditTeamMemberTrigger extends Component {
  state = {
    isOpen: false
  }

  componentDidUpdate (prevProps) {
    if (this.props.hasSubmitSucceeded && prevProps.hasSubmitSucceeded !== this.props.hasSubmitSucceeded) {
      this.closeModal()
    }
  }

  openModal = () => this.setState({ isOpen: true })
  closeModal = () => this.setState({ isOpen: false })

  render () {
    const { t, submitInviteMembers, employerId, userId } = this.props
    return userId !== employerId && (
      <Fragment>
        <SmallJustIconButton onClick={this.openModal}>
          <i className='icon-im-edit' />
        </SmallJustIconButton>
        <Modal
          title={t('modals.editTeamMembersPermissions.title')}
          submitText={t('common.save')}
          cancelText={t('common.cancel')}
          show={this.state.isOpen}
          handleSubmit={submitInviteMembers}
          handleClose={this.closeModal}
          handleCancel={this.closeModal}
          closeOnSubmit={false}
        >
          <div className='team-members-invite__form'>
            <EditTeamMemberFormConnector employerId={employerId} />
          </div>
        </Modal>
      </Fragment>
    )
  }
}

export default withTranslations(EditTeamMemberTrigger)



