import React, { PureComponent } from 'react'
import { Link } from 'react-router-dom'
import { Trans } from 'react-i18next'
import { PrimaryLightBorderedButton } from 'Shared/components/Button'
import InactiveMemberPricingImage from 'Shared/assets/images/illustrations/inactivePricingSectionLight.svg'
import withTranslations from 'Shared/hoc/withTranslations'
import { openIntercomWithNewMessage } from 'Intercom/components/IntercomComponent'
import LocationCustomConnector from '../../../../Shared/connectors/LocationCustomConnector'

class MembershipEmptyState extends PureComponent {
  render () {
    const { t, phoneNumber } = this.props
    const supportPhoneNumber = phoneNumber

    return (
      <div className='membership-details__empty-section'>
        <div className='columns'>
          <div className='column col-8 col-xs-12'>
            <div className='membership-details__empty-content'>
              <div className='membership-details__empty-heading'>{t('employer.settings.membership.empty.title')}</div>
              <div className='membership-details__empty-description'>{t('employer.settings.membership.empty.description')}</div>
              <div className='membership-details__empty-cta'>
                <Link to='/pricing'>
                  <PrimaryLightBorderedButton>
                    {t('employer.settings.membership.empty.cta')}
                  </PrimaryLightBorderedButton>
                </Link>
              </div>
              <Trans i18nKey='employer.settings.membership.empty.contact' parent='div' className='membership-details__empty-contact'>
                <a onClick={openIntercomWithNewMessage}>Talk to sales</a> or call <a href={`tel:${supportPhoneNumber}`}>{{supportPhoneNumber}}</a>
              </Trans>
            </div>
          </div>
          <div className='column col-4 col-xs-12'>
            <div className='membership-details__empty-image'>
              <img src={InactiveMemberPricingImage} />
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default withTranslations(LocationCustomConnector(MembershipEmptyState))



