import React, { Component } from 'react'
import cx from 'classnames'
import './TeamMembers.scss'
import { EmployerRoles } from 'Shared/constants/User'
import UserAvatar from 'Shared/components/UserAvatar'
import withTranslations from 'Shared/hoc/withTranslations'
import { SmallJustIconButton } from 'Shared/components/Button'
import EditTeamMemberTriggerConnector from 'Employer/connectors/TeamMembers/EditTeamMemberTriggerConnector.js'

class TeamMemberItem extends Component {
  selectTeamMemberToDelete = () => this.props.selectTeamMemberToDelete(this.props.member.id)

  render () {
    const { t, member } = this.props
    const tagClass = cx({
      'team-members__list-tag': true,
      'team-members__list-tag--primary': member.organisationRole === EmployerRoles.admin
    })

    return (
      <div className='team-members__member-item'>
        <div className='columns'>
          <div className='column col-4'>
            <div className='team-members__list-name-container'>
              <UserAvatar details={member} className='team-members__list-avatar' />
              <div className='team-members__list-fullname'>{member.firstName} {member.lastName}</div>
            </div>
          </div>
          <div className='column col-5'>
            <div className='team-members__list-email'>
              {member.email}
            </div>
          </div>
          <div className='column col-1'>
            <div className={tagClass}>
              {t(`employer.settings.teamMembers.roles.${member.organisationRole}`)}
            </div>
          </div>
          <div className='column col-2'>
            <div className='team-members__list-buttons-container'>
              <EditTeamMemberTriggerConnector employerId={this.props.member.id} />
              {
                this.props.selectTeamMemberToDelete && (
                  <SmallJustIconButton onClick={this.selectTeamMemberToDelete}><i className='ion-md-trash' /></SmallJustIconButton>
                )
              }
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default withTranslations(TeamMemberItem)



