import React, { Component } from 'react'
import { PrimaryLightBorderedButton } from 'Shared/components/Button'
import withTranslations from 'Shared/hoc/withTranslations'
import AddTeamMembersFormConnector from 'Employer/connectors/Forms/Settings/AddTeamMemberFormConnector'
import Modal from 'Shared/components/Modal'

class InviteTeamMemberTrigger extends Component {
  state = {
    isOpen: false
  }

  componentDidUpdate (prevProps) {
    if (this.props.hasSubmitSucceeded && prevProps.hasSubmitSucceeded !== this.props.hasSubmitSucceeded) {
      this.closeModal()
    }
  }

  openModal = () => this.setState({ isOpen: true })
  closeModal = () => this.setState({ isOpen: false })

  render () {
    const { t, submitInviteMembers } = this.props
    return (
      <div>
        <PrimaryLightBorderedButton onClick={this.openModal}>
          {t('employer.settings.teamMembers.emptyState.cta')}
        </PrimaryLightBorderedButton>
        <Modal
          title={t('modals.inviteTeamMembers.title')}
          submitText={t('common.sendInvite')}
          cancelText={t('common.cancel')}
          show={this.state.isOpen}
          handleSubmit={submitInviteMembers}
          handleClose={this.closeModal}
          handleCancel={this.closeModal}
          closeOnSubmit={false}
        >
          <div className='team-members-invite__form'>
            <AddTeamMembersFormConnector />
          </div>
        </Modal>
      </div>
    )
  }
}

export default withTranslations(InviteTeamMemberTrigger)



