import React from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import TeamMembersEmptyStateImage from 'Shared/assets/images/illustrations/Team_Members_Illustration.svg'
import InviteTeamMemberTriggerConnector from 'Employer/connectors/TeamMembers/InviteTeamMemberTriggerConnector'
import './TeamMembers.scss'

const TeamMembersEmptyState = ({ t }) => (
  <div>
    <div className='team-members__empty-container'>
      <div className='columns'>
        <div className='column col-5 col-xs-12'>
          <div className='team-members__empty-content'>
            <div className='team-members__empty-content-header'>
              {t('employer.settings.teamMembers.emptyState.title')}
            </div>
            <div className='team-members__empty-content-description'>
              {t('employer.settings.teamMembers.emptyState.description')}
            </div>
            <div className='team-members__empty-content-cta'>
              <InviteTeamMemberTriggerConnector />
            </div>
          </div>
        </div>
        <div className='column col-7 col-xs-12'>
          <img src={TeamMembersEmptyStateImage} />
        </div>
      </div>
    </div>
  </div>
)

export default withTranslations(TeamMembersEmptyState)



