import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import TeamMembersSettingsView from 'Employer/views/SettingsView/components/TeamMembersSettingsView'
import { getOrganisationInvites } from 'Shared/ducks/organisationInvites'
import { deleteOrganisationMember, getOrganisationEmployers } from 'Shared/ducks/employers'
import { organisationTeamMembersList, organisationAdminEmployers } from 'Shared/selectors/employers'
import { organisationInvitesList } from 'Shared/selectors/organisationInvites'
import { getTeamMembersAccess } from 'Shared/ducks/teamMembersAccess'

const mapStateToProps = state => ({
  organisationTeamMembersList: organisationTeamMembersList(state, state.user.details.organisationId),
  organisationInvitesList: organisationInvitesList(state),
  organisationAdminEmployers: organisationAdminEmployers(state, state.user.details.organisationId)
})

const mapDispatchToProps = dispatch => ({
  getOrganisationInvites: () => dispatch(getOrganisationInvites()),
  getOrganisationEmployers: () => dispatch(getOrganisationEmployers()),
  deleteOrganisationMember: id => dispatch(deleteOrganisationMember(id)),
  getTeamMembersAccess: id => dispatch(getTeamMembersAccess())
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(TeamMembersSettingsView))



