import React, { Fragment, PureComponent } from 'react'
import EmployerNotificationsFormConnector from 'Employer/connectors/Forms/Settings/EmployerNotificationsFormConnector'
import { Helmet } from 'react-helmet'
import { t } from 'i18next'

class Notifications extends PureComponent {
  render () {
    return (
      <Fragment>
        <Helmet>
          <title>{t('meta.employer.title.notifications')}</title>
          <meta property='og:title' content={t('meta.employer.title.notifications')} />
        </Helmet>
        <div>
          <EmployerNotificationsFormConnector />
        </div>
      </Fragment>
    )
  }
}

export default Notifications



