import { formValueSelector, reduxForm } from 'redux-form'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import EditTeamMembersForm from 'Employer/forms/Settings/TeamMembers/EditTeamMembersForm'
import { getJobOfferSelectedOptions } from 'Shared/selectors/jobOffers'
import { getAttachedJobOffers, getPermissionsSummary } from 'Shared/selectors/teamMembersAccess'
import { getMemberRole } from 'Shared/selectors/employers'
import { changeTeamMemberAccess } from 'Shared/ducks/teamMembersAccess'
import { generateEditTeamMemberPayload } from 'Shared/helpers/Organisation'

export const formName = 'EditTeamMembersForm'

let Form = reduxForm({
  form: formName,
  enableReinitialize: true,
  onSubmit: (values, dispatch, props) => dispatch(changeTeamMemberAccess(props.employerId, generateEditTeamMemberPayload(values)))
})(EditTeamMembersForm)

const mapStateToProps = (state, ownProps) => {
  const attachedJobs = getAttachedJobOffers(state, ownProps.employerId)
  const selectedJobOffers = getJobOfferSelectedOptions(state, attachedJobs)
  const permissionsSummary = getPermissionsSummary(state, ownProps.employerId)

  return ({
    selectedRole: formValueSelector(formName)(state, 'organisationRole'),
    initialValues: {
      ...permissionsSummary,
      jobOffers: selectedJobOffers,
      organisationRole: getMemberRole(state, ownProps.employerId)
    }
  })
}

export default withRouter(connect(mapStateToProps)(Form))



