import React, { PureComponent, Fragment } from 'react'
import PropTypes from 'prop-types'
import withTranslations from 'Shared/hoc/withTranslations'
import Label from 'Shared/components/FormElements/Label'
import { formatDate } from 'Shared/helpers/date'
import { formats } from 'Shared/constants/Date'
import Modal from 'Shared/components/Modal'
import { Trans } from 'react-i18next'
import cx from 'classnames'
import { isNil } from 'ramda'
import { getServiceAmount } from 'Shared/helpers/membership'

class MembershipServices extends PureComponent {
  state = {
    isCancelPlanModalOpened: false
  }

  openCancelPlanModal = () => this.setState({ isCancelPlanModalOpened: true })
  closeCancelPlanModal = () => this.setState({ isCancelPlanModalOpened: false })

  get enabledServices () {
    const { t, isCurrentMembershipMonthlyContinuous, enabledServices } = this.props
    return enabledServices.map(({ total, serviceKey, remaining, expiryDate }) => {
      return (
        <div className='columns membership-details__service' key={serviceKey}>
          <div className={this.serviceCellClass}>{t(`employer.settings.membership.serviceNames.${serviceKey}`)}</div>
          <div className={this.serviceCellClass}>{getServiceAmount(total, serviceKey)}</div>
          <div className={this.serviceCellClass}>{getServiceAmount(remaining, serviceKey)}</div>
          {!isCurrentMembershipMonthlyContinuous && <div
            className={this.serviceCellClass}>{isNil(expiryDate) ? '-' : formatDate(expiryDate, formats.dotted)}</div>}
        </div>
      )
    })
  }

  cancelRecurringPlan = async () => {
    const { activeCreditsRecurringTransaction, cancelRecurringTransaction } = this.props
    await cancelRecurringTransaction(activeCreditsRecurringTransaction.id)
  }

  get additionalServices () {
    const {
      t,
      enabledAdditionalServices,
      isCurrentMembershipMonthlyContinuous,
      hasActiveCreditsRecurringTransactions,
      isAdminEmployer
    } = this.props
    return enabledAdditionalServices.map(service => {
      return (
        <div className='columns membership-details__service' key={service.serviceKey}>
          <div className={this.serviceCellClass}>{t(`employer.settings.membership.serviceNames.${service.serviceKey}`)}</div>
          { !isCurrentMembershipMonthlyContinuous && <div className={this.serviceCellClass}>
            {isNil(service.expiryDate) ? '-' : formatDate(service.expiryDate, formats.dotted)}
          </div> }
          {
            isAdminEmployer && hasActiveCreditsRecurringTransactions && service.serviceKey === 'databaseSearch' &&
            <Fragment>
              <div className={this.serviceCellClass} />
              <div className={this.serviceCellClass}>
                <span onClick={this.openCancelPlanModal} className='membership-details__service-cancel'>
                  {t(`employer.settings.membership.cancelPlan.label`)}
                </span>
                <Modal
                  title={t(`employer.settings.membership.cancelPlan.title`)}
                  handleClose={this.closeCancelPlanModal}
                  handleCancel={this.closeCancelPlanModal}
                  show={this.state.isCancelPlanModalOpened}
                  submitText={t('common.confirm')}
                  handleSubmit={this.cancelRecurringPlan}
                >
                  <Trans parent='div' className='membership-details__service-cancel-description' i18nKey={'employer.settings.membership.cancelPlan.description'} expiryDate={formatDate(service.expiryDate, formats.dayYear)}>
                    By cancelling <strong>you will lose access to the world's first screened community of your
                    professional.</strong> If you confirm and end your subscription now, you can still access it
                    till <span>{formatDate(service.expiryDate, formats.dayYear)}</span>.
                  </Trans>
                </Modal>
              </div>
            </Fragment>
          }
        </div>
      )
    })
  }

  serviceCellClass = cx({
    'column': true,
    'col-3': !this.props.isCurrentMembershipMonthlyContinuous,
    'col-4': this.props.isCurrentMembershipMonthlyContinuous
  })

  render () {
    const { t, isCurrentMembershipMonthlyContinuous, enabledAdditionalServices } = this.props
    const hasAdditionalServices = enabledAdditionalServices.length > 0

    return (
      <div>
        <div className='columns'>
          <div className={this.serviceCellClass}>
            <Label>{t('employer.settings.items')}</Label>
          </div>
          <div className={this.serviceCellClass}>
            <Label>{t('employer.settings.total')}</Label>
          </div>
          <div className={this.serviceCellClass}>
            <Label>{t('employer.settings.remaining')}</Label>
          </div>
          { !isCurrentMembershipMonthlyContinuous && <div className={this.serviceCellClass}>
            <Label>{t('employer.settings.expiryDate')}</Label>
          </div> }
        </div>
        {this.enabledServices}
        {
          hasAdditionalServices && <div>
            <h4 className='membership-details__subheader'>{t('employer.settings.membershipAddOns')}</h4>
            <div className='columns'>
              <div className={this.serviceCellClass}>
                <Label>{t('employer.settings.items')}</Label>
              </div>
              { !isCurrentMembershipMonthlyContinuous && <div className={this.serviceCellClass}>
                <Label>{t('employer.settings.expiryDate')}</Label>
              </div> }
            </div>
            {this.additionalServices}
          </div>
        }
      </div>
    )
  }
}

MembershipServices.propTypes = {
  enabledServices: PropTypes.array.isRequired,
  enabledAdditionalServices: PropTypes.array.isRequired,
  isCurrentMembershipMonthlyContinuous: PropTypes.bool.isRequired
}

export default withTranslations(MembershipServices)



