import { createSelector } from 'reselect'
import { transformToArray } from 'Shared/helpers/redux'
import * as R from 'ramda'

export const getRecurringTransactions = state => state.transactions.recurringTransactions.entries
export const getOlivPlusPaymentDetails = state => state.transactions.olivPlusActivePayment

const hasCreditsAndActiveTransaction = el => R.allPass([
  R.pipe(
    R.propOr([], 'cart'),
    R.any(cartEl => R.propEq('name', 'credits', cartEl))
  ),
  R.propEq('isActive', true)
])(el)

export const hasActiveCreditsRecurringTransactions = createSelector(
  getRecurringTransactions,
  recurringTransactions => R.any(hasCreditsAndActiveTransaction, transformToArray(recurringTransactions))
)

export const activeCreditsRecurringTransaction = createSelector(
  getRecurringTransactions,
  recurringTransactions => R.find(hasCreditsAndActiveTransaction, transformToArray(recurringTransactions))
)

export const hasActiveRecurringOlivPlusPayment = createSelector(
  getOlivPlusPaymentDetails,
  details => !R.isEmpty(details)
)

export const getOlivPlusTransactionId = createSelector(
  getOlivPlusPaymentDetails,
  paymentDetails => R.propOr('', 'id', paymentDetails)
)

export const getOlivPlusNextPaymentDate = createSelector(
  getOlivPlusPaymentDetails,
  paymentDetails => R.propOr('', 'nextPaymentAt', paymentDetails)
)

export const hasFailedOlivPlusPayment = createSelector(
  getOlivPlusPaymentDetails,
  R.pipe(
    R.propOr(null, 'failedAttempts'),
    R.isNil,
    R.not
  )
)



