import React from 'react'
import EmployerSettingsView from './SettingsView'
import EmployerSettingsViewMobile from './SettingsView.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const EmployerSettingsComponent = props => props.isMobile ? <EmployerSettingsViewMobile {...props} /> : <EmployerSettingsView {...props} />

export default withDeviceWidthDetector(EmployerSettingsComponent)



