import React from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import InviteTeamMemberTriggerConnector from 'Employer/connectors/TeamMembers/InviteTeamMemberTriggerConnector'
import { TeamMemberItem } from 'Employer/components/TeamMembers'
import Label from 'Shared/components/FormElements/Label'
import './TeamMembers.scss'
import DeleteModal from 'Shared/components/DeleteModal/DeleteModal'

const renderMembers = (membersList, props) => membersList.map(member => <TeamMemberItem key={member.id} member={member} {...props} />)

class TeamMembersList extends React.Component {
  state = {
    isDeleteConfirmModalOpen: false,
    selectedTeamMemberId: null
  }

  deleteOrganisationMember = () => {
    this.props.deleteOrganisationMember(this.state.selectedTeamMemberId)
    this.closeTeamMemberDeleteModal()
  }

  closeTeamMemberDeleteModal = () => this.setState({ selectedTeamMemberId: null, isDeleteConfirmModalOpen: false })
  selectTeamMemberToDelete = id => this.setState({ selectedTeamMemberId: id, isDeleteConfirmModalOpen: true })

  teamMemberAdditionalProps = {
    selectTeamMemberToDelete: this.selectTeamMemberToDelete
  }

  render () {
    const { t, organisationTeamMembersList, organisationAdminEmployers } = this.props
    return (
      <div className='team-members__list-container'>
        <div className='team-members__list-heading-container'>
          <h3 className='team-members__list-heading'>{t('employer.settings.teamMembers.manageTeamMembers.title')}</h3>
          <InviteTeamMemberTriggerConnector />
        </div>
        <div className='team-members__list'>
          <div className='columns'>
            <div className='column col-4'><Label>{t('employer.settings.teamMembers.manageTeamMembers.teamMember')}</Label></div>
            <div className='column col-5'><Label>{t('employer.settings.teamMembers.manageTeamMembers.emailAddress')}</Label></div>
            <div className='column col-2'><Label>{t('employer.settings.teamMembers.manageTeamMembers.role')}</Label></div>
          </div>
          <div>
            {renderMembers(organisationAdminEmployers)}
            {organisationTeamMembersList.length !== 0 && renderMembers(organisationTeamMembersList, this.teamMemberAdditionalProps)}
          </div>
          <DeleteModal
            title={t('modals.deleteOrganisationMember.title')}
            content={t('modals.deleteOrganisationMember.description')}
            isModalVisible={this.state.isDeleteConfirmModalOpen}
            handleDelete={this.deleteOrganisationMember}
            hideDeleteModal={this.closeTeamMemberDeleteModal}
          />
        </div>
      </div>
    )
  }
}

export default withTranslations(TeamMembersList)



