import React, { useState } from 'react'
import { withRouter } from 'react-router-dom'
import { pure } from 'recompose'
import withTranslations from 'Shared/hoc/withTranslations'
import { PageTopHeaderMobile } from 'Shared/components/PageTopHeader'
import './SettingsView.scss'
import { SwipeEntitiesListTab, SwipeEntitiesListWrapper } from 'Shared/components/SwipeEntitiesListTabs'
import MembershipDetailsSettingsView from 'Employer/views/SettingsView/components/MembershipDetailsSettingsView'
import PersonalDetailsSettingsView from 'Employer/views/SettingsView/components/PersonalDetailsSettingsView'
import NotificationsSettingsView from 'Employer/views/SettingsView/components/Notifications'
import TeamMembersSettingsViewConnector from 'Employer/connectors/Views/Settings/TeamMembersSettingsViewConnector'

const settingsTabs = {
  membership: 'membership',
  personalDetails: 'personalDetails',
  notifications: 'notifications',
  teamMembers: 'teamMembers'
}

const EmployerSettingsViewMobile = ({ t, isAdminEmployer }) => {
  const [activeTab, setActiveTab] = useState(settingsTabs.membership)

  const changeActiveTab = (event, activeTab) => setActiveTab(activeTab)

  const getContent = () => {
    switch (activeTab) {
      case settingsTabs.membership:
        return <MembershipDetailsSettingsView />
      case settingsTabs.personalDetails:
        return <PersonalDetailsSettingsView />
      case settingsTabs.notifications:
        return <NotificationsSettingsView />
      case settingsTabs.teamMembers:
        return <TeamMembersSettingsViewConnector />
    }
  }

  return (
    <div>
      <PageTopHeaderMobile title={t('common.settings')} />
      <SwipeEntitiesListWrapper>
        <SwipeEntitiesListTab
          text={t('employer.settings.membershipDetails')}
          onClick={changeActiveTab}
          name={settingsTabs.membership}
          isActive={activeTab === settingsTabs.membership}
        />
        <SwipeEntitiesListTab
          text={t('employer.settings.personalDetails')}
          onClick={changeActiveTab}
          name={settingsTabs.personalDetails}
          isActive={activeTab === settingsTabs.personalDetails}
        />
        <SwipeEntitiesListTab
          text={t('employer.settings.notifications')}
          onClick={changeActiveTab}
          name={settingsTabs.notifications}
          isActive={activeTab === settingsTabs.notifications}
        />
        {isAdminEmployer && <SwipeEntitiesListTab
          text={t('employer.settings.teamMembers.title')}
          onClick={changeActiveTab}
          name={settingsTabs.teamMembers}
          isActive={activeTab === settingsTabs.teamMembers}
        />}
      </SwipeEntitiesListWrapper>
      {getContent()}
    </div>
  )
}

export default pure(withRouter(withTranslations(EmployerSettingsViewMobile)))



