import { connect } from 'react-redux'
import { submit, hasSubmitSucceeded } from 'redux-form'
import { formName } from 'Employer/connectors/Forms/Settings/EditTeamMemberFormConnector'
import EditTeamMemberTrigger from 'Employer/components/TeamMembers/EditTeamMemberTrigger'

const mapStateToProps = state => ({
  hasSubmitSucceeded: hasSubmitSucceeded(formName)(state),
  userId: state.user.details.id
})

const mapDispatchToProps = dispatch => ({
  submitInviteMembers: () => dispatch(submit(formName))
})

export default connect(mapStateToProps, mapDispatchToProps)(EditTeamMemberTrigger)



