import { connect } from 'react-redux'
import MembershipNotificationAlert from 'Employer/components/MembershipNotificationAlert/MembershipNotificationAlert'
import {
  hasCurrentMembership,
  getCurrentMembershipStatus,
  isCurrentMembershipExpired
} from 'Shared/selectors/membership'

const mapStateToProps = state => ({
  hasCurrentMembership: hasCurrentMembership(state),
  getCurrentMembershipStatus: getCurrentMembershipStatus(state),
  isCurrentMembershipExpired: isCurrentMembershipExpired(state)
})

const mapDispatchToProps = dispatch => ({})

export default connect(mapStateToProps, mapDispatchToProps)(MembershipNotificationAlert)



