import React, { Component } from 'react'
import PermissionsFormFields from 'Employer/connectors/TeamMembers/AddTeamMembersPrmissionsFieldsConnector'
import AssignJobPostFormFieldConnector from 'Employer/connectors/Forms/AssignJobPostFormField/AssignJobPostFormFieldConnector'
import withTranslations from 'Shared/hoc/withTranslations/index'
import { SelectRadio } from 'Shared/components/FormElements'
import { EmployerRoles } from 'Shared/constants/User'
import { Field } from 'redux-form'

class EditTeamMembersForm extends Component {
  render () {
    const { t, handleSubmit, form, selectedRole } = this.props
    return (
      <div>
        <form onSubmit={handleSubmit}>
          <div>
            <Field
              component={SelectRadio}
              options={[
                {
                  label: t('forms.permissions.admin.label'),
                  value: EmployerRoles.admin,
                  description: t('forms.permissions.admin.description'),
                  isSelected: selectedRole === EmployerRoles.admin
                },
                {
                  label: t('forms.permissions.manager.label'),
                  value: EmployerRoles.organisation_manager,
                  description: t('forms.permissions.manager.description'),
                  isSelected: selectedRole === EmployerRoles.organisation_manager
                }
              ]}
              name='organisationRole'
              placeholder={t('forms.teamMembers.placeholder')}
              withIcon
            />
          </div>
          <PermissionsFormFields formName={form} />
          <AssignJobPostFormFieldConnector formName={form} />
        </form>
      </div>
    )
  }
}

export default withTranslations(EditTeamMembersForm)



