import React, { Component } from 'react'
import { Field } from 'redux-form'
import { SwitchToggle } from 'Shared/components/FormElements'
import withTranslations from 'Shared/hoc/withTranslations'
import './PermissionsFormFields.scss'

const ToggleLabel = ({ label, description }) => (
  <div>
    <div className='permission-form-fields__label'>{label}</div>
    <div className='permission-form-fields__label-description'>{description}</div>
  </div>
)

class PermissionsFormFields extends Component {
  componentDidMount () {
    if (this.props.selectedAdmin) {
      this.props.changePermission('applications', true)
      this.props.changePermission('job_posts', true)
      this.props.changePermission('cv_search', true)
      this.props.changePermission('company_profile', true)
      this.props.changePermission('purchase', true)
    }
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    if (prevProps.selectedAdmin !== this.props.selectedAdmin && this.props.selectedAdmin) {
      this.props.changePermission('applications', true)
      this.props.changePermission('job_posts', true)
      this.props.changePermission('cv_search', true)
      this.props.changePermission('company_profile', true)
      this.props.changePermission('purchase', true)
    }
  }

  render () {
    const { t, selectedAdmin } = this.props

    return (
      <div className='permission-form-fields'>
        <div className='permission-form-fields__header'>
          {t('forms.permissions.title')}
        </div>
        <div className='permission-form-fields__field'>
          <Field
            component={SwitchToggle}
            labelPosition='left'
            disabled={selectedAdmin}
            selectedDisableIcon={<div className='permission-form-fields__admin' />}
            label={
              <ToggleLabel
                label={t('forms.permissions.applications.label')}
                description={t('forms.permissions.applications.description')}
              />
            }
            format={v => !!v}
            name='applications'
          />
        </div>
        <div className='permission-form-fields__field'>
          <Field
            component={SwitchToggle}
            labelPosition='left'
            disabled={selectedAdmin}
            selectedDisableIcon={<div className='permission-form-fields__admin' />}
            label={
              <ToggleLabel
                label={t('forms.permissions.jobPosts.label')}
                description={t('forms.permissions.jobPosts.description')}
              />
            }
            format={v => !!v}
            name='job_posts'
          />
        </div>
        <div className='permission-form-fields__field'>
          <Field
            component={SwitchToggle}
            labelPosition='left'
            disabled={selectedAdmin}
            selectedDisableIcon={<div className='permission-form-fields__admin' />}
            label={
              <ToggleLabel
                label={t('forms.permissions.cvSearch.label')}
                description={t('forms.permissions.cvSearch.description')}
              />
            }
            format={v => !!v}
            name='cv_search'
          />
        </div>
        <div className='permission-form-fields__field'>
          <Field
            component={SwitchToggle}
            labelPosition='left'
            disabled={selectedAdmin}
            selectedDisableIcon={<div className='permission-form-fields__admin' />}
            label={
              <ToggleLabel
                label={t('forms.permissions.companyProfile.label')}
                description={t('forms.permissions.companyProfile.description')}
              />
            }
            format={v => !!v}
            name='company_profile'
          />
        </div>
        <div className='permission-form-fields__field'>
          <Field
            component={SwitchToggle}
            labelPosition='left'
            disabled={selectedAdmin}
            selectedDisableIcon={<div className='permission-form-fields__admin' />}
            label={
              <ToggleLabel
                label={t('forms.permissions.purchasing.label')}
                description={t('forms.permissions.purchasing.description')}
              />
            }
            format={v => !!v}
            name='purchase'
          />
        </div>
      </div>
    )
  }
}

export default withTranslations(PermissionsFormFields)



