import { reduxForm } from 'redux-form'
import { t } from 'i18next'
import DeleteAccountForm from 'Shared/forms/DeleteAccount/DeleteAccountForm'
import NotificationService from 'Shared/services/NotificationService'
import validate from './EmployerDeleteAccountValidator'
import UserService from 'Shared/services/UserService'

const userService = new UserService()

export const formName = 'EmployerDeleteAccountForm'

let Form = reduxForm({
  form: formName,
  validate,
  onSubmit: async () => {
    try {
      await userService.deleteEmployerAccount()
      return NotificationService.success(t('alerts.deleteEmployerAccountSuccess'))
    } catch (e) {
      return NotificationService.error(t('alerts.deleteEmployerAccountFailed'))
    }
  }
})(DeleteAccountForm)

export default Form



