import React, { Component, Fragment } from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import { Field } from 'redux-form'
import JobOfferMultiSelect from 'Shared/components/FormElements/JobOfferMultiSelect'
import './AssignJobPostsFormField.scss'

class AssignJobPostsFormField extends Component {
  state = {
    selectedJobStatuses: ['all']
  }

  componentDidMount () {
    this.props.fetchJobOffers(this.props.organisationId)
  }

  render () {
    const { t, selectedAdmin } = this.props

    return (
      <div>
        {
          !selectedAdmin && (
            <Fragment>
              <div className='assign-job-post__heading'>
                <div className='assign-job-post__title'>{t('forms.assignJobPosts.title')}</div>
                <div className='assign-job-post__description'>{t('forms.assignJobPosts.descriptionMember')}</div>
              </div>
              <div className='assign-job-post__select'>
                <Field
                  component={JobOfferMultiSelect}
                  label={t('forms.assignJobPosts.jobPosts')}
                  name='jobOffers'
                  options={this.props.getJobOfferOptions(['all'])}
                />
              </div>
            </Fragment>
          )
        }
      </div>
    )
  }
}

export default withTranslations(AssignJobPostsFormField)



