import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import AssignJobPostFormField from 'Employer/forms/AssignJobPostsFormField'
import { fetchOrganisationJobOffersByOrganisationId } from 'Shared/ducks/jobOffers'
import { getJobOfferSelectOptionsByStatuses } from 'Shared/selectors/jobOffers'
import { formValueSelector } from 'redux-form'
import { EmployerRoles } from 'Shared/constants/User'

const mapStateToProps = (state, ownProps) => ({
  organisationId: state.user.details.organisationId,
  getJobOfferOptions: statuses => getJobOfferSelectOptionsByStatuses(state, statuses),
  selectedAdmin: formValueSelector(ownProps.formName)(state, 'organisationRole') === EmployerRoles.admin
})

const mapDispatchToProps = dispatch => ({
  fetchJobOffers: organisationId => dispatch(fetchOrganisationJobOffersByOrganisationId(organisationId))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(AssignJobPostFormField))



