import React, { Component } from 'react'
import { Field } from 'redux-form'
import { Input, PhoneInput, SingleImageUploader } from 'Shared/components/FormElements'
import { PrimaryButton } from 'Shared/components/Button'
import withTranslations from 'Shared/hoc/withTranslations'

import './PersonalDetails.scss'
import { Helmet } from 'react-helmet'

class ChangePasswordForm extends Component {
  uploadTextContent = {
    uploadButtonText: 'common.changePhoto',
    uploaderContent: 'common.uploadEmployerProfilePhotoInfo',
    editorModalTitle: 'common.createProfilePhoto',
    deleteModalTitle: 'remove.photo.title',
    deleteModalContent: 'remove.photo.description'
  }
  render () {
    const { t, error, submitting, handleSubmit } = this.props
    return (
      <React.Fragment>
        <Helmet>
          <title>{t('meta.employer.title.personalDetails')}</title>
          <meta property='og:title' content={t('meta.employer.title.personalDetails')} />
        </Helmet>

        <div className='personal-details-form__container'>
          <h3 className='personal-details-form__section-title'>{t('common.changePersonalDetails')}</h3>
          <form className='personal-details-form__form' onSubmit={handleSubmit(this.props.updateEmployer)}>
            <SingleImageUploader
              textConfig={this.uploadTextContent}
              label={t('forms.profilePicture.label')}
              uploadFile={this.props.uploadAvatar}
              deleteFile={this.props.deleteAvatar}
              imageUrl={this.props.photoUrl}
            />
            <Field
              component={Input}
              label={t('forms.firstName.label')}
              name='firstName'
              placeholder={t('forms.firstName.placeholder')}
              type='text'
            />
            <Field
              component={Input}
              label={t('forms.lastName.label')}
              name='lastName'
              placeholder={t('forms.lastName.placeholder')}
              type='text'
            />
            <Field
              component={PhoneInput}
              className={'form-group'}
              label={t('forms.phoneNumber.label')}
              name='phoneNumber'
            />
            <Field
              component={Input}
              name='id'
              type='hidden'
            />
            <PrimaryButton
              className='personal-details-form__submit'
              disabled={error || submitting}
              type='submit'>
              {submitting ? `${t('common.loading')}...` : t('common.changeDetails')}
            </PrimaryButton>
          </form>
        </div>
      </React.Fragment>
    )
  }
}

export default withTranslations(ChangePasswordForm)



