import React from 'react'
import { Switch, Route, Redirect } from 'react-router-dom'
import Settings from 'Shared/layouts/Settings/Settings'
import PersonalDetailsSettingsView from './components/PersonalDetailsSettingsView'
import MembershipDetailsSettingsView from './components/MembershipDetailsSettingsView'
import TeamMembersSettingsViewConnector from 'Employer/connectors/Views/Settings/TeamMembersSettingsViewConnector'
import NotificationsSettingsView from './components/Notifications'
import { EntitiesListTabWrapper, EntitiesListTabLink } from 'Shared/components/EntitiesListTabs'
import EmployerPrivateRoute from 'Employer/components/EmployerPrivateRoute'
import withTranslations from 'Shared/hoc/withTranslations'
import { PageTopHeaderMobile } from 'Shared/components/PageTopHeader'
import './SettingsView.scss'

const EmployerSettingsView = ({ t, isAdminEmployer }) => (
  <div className='settings-view__container parsing--hidden'>
    <PageTopHeaderMobile title={t('common.settings')} />
    <Settings>
      <EntitiesListTabWrapper>
        <EntitiesListTabLink to='/employer/settings/membership-details' text={t('employer.settings.membershipDetails')} />
        <EntitiesListTabLink to='/employer/settings/personal-details' text={t('employer.settings.personalDetails')} />
        <EntitiesListTabLink to='/employer/settings/notifications' text={t('employer.settings.notifications')} />
        {isAdminEmployer && <EntitiesListTabLink to='/employer/settings/team-members' text={t('employer.settings.teamMembers.title')} /> }
      </EntitiesListTabWrapper>
      <Switch>
        <Route path='/employer/settings/membership-details' component={MembershipDetailsSettingsView} />
        <Route path='/employer/settings/personal-details' component={PersonalDetailsSettingsView} />
        <Route path='/employer/settings/notifications' component={NotificationsSettingsView} />
        <EmployerPrivateRoute allowedFor={[]} path='/employer/settings/team-members' component={TeamMembersSettingsViewConnector} />
        <Redirect from='/employer/settings' to='/employer/settings/membership-details' />
      </Switch>
    </Settings>
  </div>
)

export default withTranslations(EmployerSettingsView)



