import { connect } from 'react-redux'
import { submit, hasSubmitSucceeded } from 'redux-form'
import { formName } from 'Employer/connectors/Forms/Settings/AddTeamMemberFormConnector'
import InviteTeamMemberTrigger from 'Employer/components/TeamMembers/InviteTeamMemberTrigger'

const mapStateToProps = state => ({
  hasSubmitSucceeded: hasSubmitSucceeded(formName)(state)
})

const mapDispatchToProps = dispatch => ({
  submitInviteMembers: () => dispatch(submit(formName))
})

export default connect(mapStateToProps, mapDispatchToProps)(InviteTeamMemberTrigger)



