import React, { Component } from 'react'
import Button from 'Shared/components/Button'
import DeleteModal from 'Shared/components/DeleteModal'
import withTranslations from 'Shared/hoc/withTranslations'
import './TeamMembers.scss'

class PendingInviteItem extends Component {
  state = {
    showDeleteModal: false
  }

  openDeleteModal = () => this.setState({ showDeleteModal: true })
  closeDeleteModal = () => this.setState({ showDeleteModal: false })

  render () {
    const { t, invite, deleteOrganisationInvite } = this.props

    return (
      <div className='team-members__invite-list-item'>
        <div className='columns'>
          <div className='column col-9'>
            <div className='team-members__invite-email'>{invite.email}</div>
          </div>
          <div className='column col-3'>
            <div className='team-members__invite-details'>
              <div className='team-members__list-tag'>{t('employer.settings.teamMembers.pendingInvitations.inviteSent')}</div>
              <Button className='team-members__list-btn' onClick={this.openDeleteModal} justIcon><i className='ion-md-trash' /></Button>
            </div>
          </div>
        </div>
        <DeleteModal
          title={t('remove.teamMemberInvitation.title')}
          handleDelete={deleteOrganisationInvite}
          isModalVisible={this.state.showDeleteModal}
          hideDeleteModal={this.closeDeleteModal}
        />
      </div>
    )
  }
}

export default withTranslations(PendingInviteItem)



