import { reduxForm } from 'redux-form'
import { connect } from 'react-redux'
import { t } from 'i18next'
import NotificationsForm from 'Employer/forms/Settings/Notifications/NotificationsForm'
import { updateEmployer } from 'Shared/ducks/user/employer'
import NotificationService from 'Shared/services/NotificationService'

export const formName = 'EmployerNotificationsForm'

let Form = reduxForm({
  form: formName,
  enableReinitialize: true,
  onSubmit: async (values, dispatch, ownProps) => {
    try {
      await dispatch(updateEmployer(ownProps.employerId, values))
      return NotificationService.success(t('alerts.formSuccessfullySaved'))
    } catch (e) {
      NotificationService.error(t('alerts.savingFormFailed'))
      return Promise.reject(e)
    }
  }
})(NotificationsForm)

const mapStateToProps = state => {
  const {
    applicationNotificationEnabled,
    weeklyCandidateEmailEnabled,
    weeklyActiveJobSummaryEnabled,
    id
  } = state.user.details

  return ({
    employerId: id,
    initialValues: {
      applicationNotificationEnabled,
      weeklyCandidateEmailEnabled,
      weeklyActiveJobSummaryEnabled
    }
  })
}

export default connect(mapStateToProps)(Form)



