import React from 'react'
import { connect } from 'react-redux'
import { isInvalid, submit, isSubmitting, hasSubmitSucceeded } from 'redux-form'
import EmployerDeleteAccountConnector, { formName } from 'Employer/connectors/Forms/Settings/EmployerDeleteAccountConnector'
import DeleteAccountSection from 'Shared/components/DeleteAccountSection/DeleteAccountSection'

const mapStateToProps = state => ({
  isDeleteAccountFormInvalid: isInvalid(formName)(state),
  isDeleteAccountFormSubmitting: isSubmitting(formName)(state),
  isDeleteAccountFormSubmitSucceeded: hasSubmitSucceeded(formName)(state),
  getDeleteAccountConnector: () => <EmployerDeleteAccountConnector />,
  userRole: state.user.role
})

const mapDispatchToProps = dispatch => ({
  submitDeleteAccountForm: () => dispatch(submit(formName))
})

export default connect(mapStateToProps, mapDispatchToProps)(DeleteAccountSection)



