import { connect } from 'react-redux'
import { formValueSelector, change } from 'redux-form'
import PermissionsFormFields from 'Employer/forms/PermissionsFormFields'
import { EmployerRoles } from 'Shared/constants/User'

const mapStateToProps = (state, ownProps) => ({
  selectedAdmin: formValueSelector(ownProps.formName)(state, 'organisationRole') === EmployerRoles.admin
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  changePermission: (fieldname, value) => dispatch(change(ownProps.formName, fieldname, value))
})

export default connect(mapStateToProps, mapDispatchToProps)(PermissionsFormFields)



